<template>
  <el-button size="medium" type="text" @click="screenFull()">
    <ea-icon :icon-class=" 'fullScreen'" />
  </el-button>
</template>
<script>
import screenfull from "screenfull";
export default {
  name:"EaFullscreen",
  data() {
    return {
      isFullScreen:false
    };
  },
  methods: {
    screenFull() {
      if (!screenfull.isEnabled) {
        this.$message({
          message: "不支持此功能",
          type: "warning"
        });
        return false;
      }
      screenfull.toggle();
    }
  }
};
</script>